







import { getArrayObjIndexByKey } from '@/utils'
import { Component, Vue, Watch } from 'vue-property-decorator'
import { DebugDialog, TabBar } from '@/pages/msfWap/components/layout/index.ts'
@Component({
  components: {
    DebugDialog, TabBar
  }
})
export default class ViewComponent extends Vue {
  /**data */
  isShowTabBar = true
  isShowDebugDialog = false
  propTabBar = {}
  /**watch */
  @Watch("$route") handleRouteChange(to: any) {
    if (location.host.indexOf('daokoudai') < 0) { // 非生产环境始终展示底栏
      return;
    }
    /**控制布局选项卡的显示 */
    const tabs = (TabBar as any).data().tabs || [];
    const path = to.path;
    if (getArrayObjIndexByKey(tabs, { path }) < 0) {
      this.isShowTabBar = false
    } else {
      this.isShowTabBar = true
    }
  }
  /**life hook */
  created() {
    // if (location.host.indexOf('daokoudai') < 0) {
    if (!this.$store.state.isProdEnvUrl) { // 非生产或类生产环境
      this.isShowDebugDialog = true;
      this.propTabBar = {
        tab: {
          label: '测试',
          icon: 'cubeic-tag',
          path: '/test',
        }
      }

    }
  }
}
