






import { Component, Vue } from 'vue-property-decorator'

@Component({
  components: {}
})
export default class MyComponent extends Vue {
  /**data */
  domainNodeServer = '' // 本机中层
  debugNodeServer = this.$store.state.debugNodeServer.slice(2) // 线上调试中层


  /**life hook */
  created(){
    const middleServerPort = location.protocol ==='https:' ? ':5001' : ':5000';
    this.domainNodeServer = location.port ? (location.hostname + middleServerPort) : location.host;
  
  }

  // /**computed */
  // get visible() { // 展示参数控制方法
  //   return location.port !== "" || location.host == "doudizhu.github.io" || location.host === '39.100.122.36' ||  location.host === 'dkdmsf.com' || location.host === 'lihaipeng.net';
  // }
  /**method */
  showClose() {
    this.$createDialog({
      type: 'prompt',
      title: '域名配置',
      content: `
        默认为本机:
        <br>${this.domainNodeServer}
        <br>线上调试可用：
        <br>${this.debugNodeServer}
        <br>msf.daokoujihua.com
        <br>api.daokouhua.com
        <br>直连请直接输入：
      `,
      prompt: {
        value: this.$store.state.domainApi,
        placeholder: '输入api域名配置'
      },
      onConfirm: (e:any, promptValue:any) => {
        this.$store.commit('setDomainApi',promptValue)
        this.$createToast({
          type: 'warn',
          time: 2000,
          txt: `请求api域名已修改为: ${promptValue || ''}`
        }).show()
      }
    }).show()
  }
}
