import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate' // vuex数据持久化
import { getTld,isProdEnvUrl } from "@/utils/common";

Vue.use(Vuex)

// node服务器域名
const middleServerPort = location.protocol ==='https:' ? ':5001' : ':5000';
const domainNodeServer = location.port ? (location.hostname + middleServerPort) : location.host;
const originNodeServer = `${location.protocol}//${domainNodeServer}`; // node服务器url
// 多页面history兼容(开发环境)
const projectNamePathIndex = (location.pathname).indexOf('.html')
// let base = process.env.BASE_URL;
let projectUrlBase = '';  // 当前项目根路径名
if(projectNamePathIndex >= 0 ){
  const projectName = location.pathname.slice(1,projectNamePathIndex);
  projectUrlBase = `${location.origin}/${projectName}.html`
}
const originReturnUrl = `${originNodeServer}${projectUrlBase}`;

const tld = getTld(location.hostname)

// 单独定义初始值模板
const stateTpl = {
  token: '',
  user: {},
  // domainApi: (location.host === "doudizhu.github.io") ? 'api.daokoujihua.com' : '', // api服务器域名
  domainApi: tld === 'github.io' ? 'mevn-work.herokuapp.com' : '', // api服务器域名
  domainNodeServer: '//'+domainNodeServer, // 调试配置服务器url
  originReturnUrl, // node服务器url(调试服务器 || 默认本地服务) + 当前项目名称
  debugNodeServer: '//mevn-work.herokuapp.com', // //mevn-work.herokuapp.com 或 //dkdmsf.com 或 //daokoujihua.com
  originNodeServer, // 原始默认node服务器

  tld, // 获取一级域名
  isProdEnvUrl: isProdEnvUrl(), // 是否生产或类生产环境
}
// console.log('stateTpl:',stateTpl)

export default new Vuex.Store({
  state: {
    ...stateTpl,
  },
  mutations: {
    setToken(state, token){ 
      state.token = token
    },
    setUser(state, user){ 
      state.user = user
    },
    setDomainApi(state, domainApi){ 
      state.domainApi = domainApi

      state.domainNodeServer = `${domainApi ? `${location.protocol}//${domainApi}` : state.originNodeServer}`
      state.originReturnUrl =`${state.domainNodeServer}/${projectUrlBase}`
    },

    // 重置vuex
    resetAll(state) {
      const domainApi = state.domainApi; // 备份恢复调试配置
      Object.assign(state, stateTpl) // 要用Object.assign而不是直接给state赋值。  
      if (!stateTpl.isProdEnvUrl) { // 非生产或类生产环境，还原调试配置
        this.commit('setDomainApi', domainApi)
      }
    }
  },
  actions: {

  },
  plugins: [
    createPersistedState(),
  ],
})
